import React from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { cartActions } from "./store/index";
import {
  Center,
  Box,
  Text,
  SimpleGrid,
  Button,
} from "@chakra-ui/react";
import MenhirWindow from "./Window";
import Product from "./model/Product";
import { AiOutlineShoppingCart } from "react-icons/ai";
import MenhirOrderSummary from "./OrderSummary";
import CartItem from "./CartItem";

export default function MenhirCart(props:any) {
  const dispatch = useDispatch();

  const toggleCounter = () => {
    dispatch({ type: "TOGGLE" });
  };

  const incHandler = () => {
    dispatch({ type: "ADD", amount: 5 });
  };

  const decHandler = () => {
    dispatch({ type: "DEC" });
  };

  const removeProduct = (code) => {
    const prod: Product = {
      Code: code,
    };
    dispatch(cartActions.rem({ amount: 1, product: prod }));
  };

  const cart = useSelector((state) => state.cart);
  const itemCount = cart.length;
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.Discount;
    const discountedPrice = val.Price - (val.Price * discount) / 100;
    return acc + discountedPrice;
  }, 0);
  const shippingCost = 7.0;
  const totalCart = totalItems + shippingCost;

  return (
    <Box>
      <MenhirWindow showHomeBanner={false} />
      {cart && cart.length > 0 && (
        <Text as="h3" mt={{ base: 0, md: 10 }}>
          IL TUO CARRELLO
        </Text>
      )}

      {!cart ||
        (cart.length === 0 && (
          <Box>
            <Text as="h3" mt={{ base: 0, md: 10 }}>
              CARRELLO VUOTO
            </Text>
            <Center>
              <Box mr={3} color="teal.600">
                <AiOutlineShoppingCart size="40px" />
              </Box>
            </Center>
          </Box>
        ))}

      {cart && cart.length > 0 && (
        <SimpleGrid columns={2} spacing={10} minChildWidth="300px" p={4}>
          <Box>
            {cart.map((item) => (
              <CartItem item={item} />
            ))}
          </Box>
          <MenhirOrderSummary />
        </SimpleGrid>
      )}

      {cart && cart.length > 0 && (
        <Center>
          <Link to="/checkoutship">
            <Button
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade"}}
              size="md"
              fontWeight="normal"
            >
              VAI IN CASSA
            </Button>
          </Link>
        </Center>
      )}

      {!cart ||
        (cart.length === 0 && (
          <Center>
            <Link to="/browser">
              <Button
                mt={10}
                mb={10}
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade"}}
                size="md"
                fontWeight="normal"
              >
                SFOGLIA I PRODOTTI
              </Button>
            </Link>
          </Center>
        ))}
    </Box>
  );
}
