import React, { useState, useEffect } from "react";
import './Browser.css';

import {
  Box,
  Flex,
  SimpleGrid,
  Text
} from "@chakra-ui/react";

import MenhirWindow from "./Window";
import MenhirMiniProduct from "./MiniProduct";

export default function MenhirBrowser(props) {

  console.log('browser here!');

  const [data, setData] = useState([]);
  const [catTitle, setCatTitle] = useState('ALL');

  const getData = () => {
    fetch("./test-data-items.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        const catId = props.location.state ? props.location.state[0]['catId'] : undefined;
        if (catId) { 
          setData(myJson.filter(k => k.category === parseInt(catId)));
          setCatTitle(props.location.state[1]['catDesc']);
        }
        else setData(myJson);
      });
  };

  useEffect(() => {
    console.log('useEffect fired');
    getData();
  }, props.location.state || []);

  return (
    <Box>
      <Flex color="white">
        <Box flex="1" p={0}>
          <MenhirWindow showHomeBanner={false} />
          <Box><Text as="h3" mt={{ base:0, md:5}}>{catTitle}</Text></Box>
          <SimpleGrid minChildWidth="175px" columns={2} spacing="4px" ml={1}>
            {data.map((item, index) => (
              <MenhirMiniProduct
              key={index}
              code={item.code}
              title={item.title}
              description={item.description}
              discount={item.discount}
              price={item.price}
              category={item.category}
              rating={item.rating}
              imgName={item.imgName}
              minimalView={true}
              longDescription={item.longDescription}
              ></MenhirMiniProduct>
            ))}
          </SimpleGrid>          
        </Box>
      </Flex>
    </Box>
  );
}
