import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import "./Checkout.css";
import { CountryDropdown, RegionDropdown } from "react-country-region-selector";
import {
  Center,
  Box,
  Text,
  FormControl,
  FormLabel,
  SimpleGrid,
  Input,
  Flex,
  Button,
} from "@chakra-ui/react";
import MenhirOrderSummary from "./OrderSummary";

export default function MenhirCheckoutShip(props:any) {
  const cart = useSelector((state) => state.cart);
  const itemCount = cart.length;
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.Discount;
    const discountedPrice = val.Price - (val.Price * discount) / 100;
    return acc + discountedPrice;
  }, 0);
  const shippingCost = 7.0;
  const totalCart = totalItems + shippingCost;

  const [country, setCountry] = useState("");
  const [region, setRegion] = useState("");

  const selectCountry = (val: any) => {
    setCountry(val);
  };

  const selectRegion = (region: any) => {
    setRegion(region);
  };

  return (
    <Flex>
      <Box flex="1" p={0}>
        {cart && cart.length > 0 && (
          <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
            CASSA / INDIRIZZO DI CONSEGNA
          </Text>
        )}

        {cart && cart.length > 0 && (
          <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
            <Box className="menhir-input-form">
              <Text as="h4" mt={0} mb={4} fontWeight="bold">
                IL TUO INDIRIZZO PER LA CONSEGNA
              </Text>
              <FormControl id="address-street">
                <FormLabel>Via</FormLabel>
                <Input type="text" />
              </FormControl>
              <SimpleGrid columns={2} spacing={4} mt={4}>
                <FormControl>
                  <FormLabel>Città</FormLabel>
                  <Input type="text" />
                </FormControl>
                <FormControl>
                  <FormLabel>CAP</FormLabel>
                  <Input type="text" />
                </FormControl>
                <Box>
                  <FormLabel>Stato</FormLabel>
                  <CountryDropdown
                    value={country}
                    onChange={(val) => selectCountry(val)}
                  />
                </Box>
                <Box>
                  <FormLabel>Regione</FormLabel>
                  <RegionDropdown
                    country={country}
                    value={region}
                    onChange={(val) => selectRegion(val)}
                  />
                </Box>
                <FormControl>
                  <FormLabel>Numero di telefono</FormLabel>
                  <Input type="text" />
                </FormControl>
              </SimpleGrid>
            </Box>
            <MenhirOrderSummary />
          </SimpleGrid>
        )}

        <Center mt={4}>
          <Link to="/cart">
            <Button
              mt={10}
              mb={10}
              backgroundColor="lightgray"
              size="md"
              fontWeight="normal"
              mr={4}
            >
              INDIETRO
            </Button>
          </Link>
          <Link to="/checkoutpay">
            <Button
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade"}}
              size="md"
              fontWeight="normal"
            >
              CONFERMA
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
