import React from "react";
import { useDispatch } from "react-redux";
import { cartActions } from "./store/index";
import { Box, Text, Image, Spacer } from "@chakra-ui/react";
import { MenhirPriceTag } from "./Price";
import { MdClear } from "react-icons/md";

export default function MenhirCartItem(props: any) {
  const dispatch = useDispatch();
  const item = props.item;
  const removeProduct = (code) => {
    const prod: Product = {
      Code: code,
    };
    dispatch(cartActions.rem({ amount: 1, product: prod }));
  };

  return (
    <Box bgColor="whitesmoke" p={2} mb={2}>
      <Box display="flex">
        <Image
          maxW="100px"
          mr={2}
          align="center"
          src={require(`./demo-images/${item.ImgName}`).default}
        />
        <Box minW="50%">
          <Text as="h4">{item.Title}</Text>
          <Text as="p" maxW="200px" fontSize="14px">
            {item.Description}
          </Text>
        </Box>
        <Spacer />
        <Box color="#666666" cursor="pointer">
          <MdClear size="22px" onClick={() => removeProduct(`${item.Code}`)} />
        </Box>
      </Box>
      <Box display="flex" justifyContent="end">
        <MenhirPriceTag
          discountedPrice=""
          price={item.Price}
          discount={item.Discount}
        />
      </Box>
    </Box>
  );
}
