import { createSlice, configureStore } from '@reduxjs/toolkit';

const initState = { cart: [] };

const  cartSlice = createSlice({
  name : 'menhirStore',
  initialState: initState,
  reducers: {
    add(state, action) {
      for (let i=0;i<action.payload.amount;i++) state.cart.push(action.payload.product);
    },
    rem(state, action) {
      state.cart = state.cart.filter(i => i['Code'] !== action.payload.product.Code);
    },
    clear(state, action) {
      state.cart = [];
    }
  }
});

