import React, { useState, useEffect } from "react";
import {
  Box,
  Stack,
  HStack,
  Text,
  Flex,
  useMediaQuery,
} from "@chakra-ui/react";
import MenhirMiniProduct from "./MiniProduct";
import { Link } from "react-router-dom";
import "./CategoryPreview.css";

const CheckMobile = () => {
  const [isLargerThan220] = useMediaQuery("(min-width: 380px)");
  console.log(".." + isLargerThan220);
  return isLargerThan220 ? 4 : 2;
};

const MenhirCategoryPreview = (props: any) => {
  
  const [howMany, sethowMany] = useState(CheckMobile());
  const [data, setData] = useState([]);
  
  const getData = () => {
    fetch("./test-data-items.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        myJson = myJson.filter((k, i) => k.category === props.category);
        setData(myJson);
      });
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <Box
      bg="white"
      w="100%"
      mt={{ base: 5, md: 10 }}
      mb={10}
      borderColor="gray.200"
      display="flex"
      justifyContent="center"
    >
      <Stack spacing={3} padding={2}>
        <Flex justifyContent="space-between">
          <Text
            as="h4"
            mb={2}
            textTransform="uppercase"
            color="teal.600"
            fontSize="md"
            textAlign="center"
          >
            {props.title}
          </Text>

          <Link to={{ pathname: "/browser" }} className="see-all-link" fontSize="sm" color="teal.600">
            Vedi tutti
          </Link>
        </Flex>
        <HStack>
          {data.map(
            (item, index) =>
              index < howMany && (
                <MenhirMiniProduct
                  key={index}
                  code={item.code}
                  title={item.title}
                  description={item.description}
                  discount={item.discount}
                  price={item.price}
                  rating={item.rating}
                  imgName={item.imgName}
                  minimalView={true}
                  longDescription={item.longDescription}
                ></MenhirMiniProduct>
              )
          )}
        </HStack>
      </Stack>
    </Box>
  );
};

export default MenhirCategoryPreview;
