import React from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import creditcard from "./credit-card-image-small.png";
import paypal from "./paypal-image-small.png";
import {
  Center,
  Stack,
  Radio,
  Box,
  Image,
  Text,
  SimpleGrid,
  Button,
  RadioGroup,
  Flex,
} from "@chakra-ui/react";
import MenhirOrderSummary from "./OrderSummary";

export default function MenhirCheckoutPay(props) {
  const cart = useSelector((state) => state.cart);
  const itemCount = cart.length;
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.Discount;
    const discountedPrice = val.Price - (val.Price * discount) / 100;
    return acc + discountedPrice;
  }, 0);
  const shippingCost = 7.0;
  const totalCart = totalItems + shippingCost;

  return (
    <Flex>
      <Box flex="1" p={0}>

        {cart && cart.length > 0 && (
          <Box>
            <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
              CHECKOUT / PAY
            </Text>
          </Box>
        )}

        {cart && cart.length > 0 && (
          <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
            <Box className="menhir-input-form">
              <Text as="h4" mt={0} mb={4} fontWeight="bold">
                MODALITA' DI PAGAMENTO
              </Text>
              <Box>
                <RadioGroup defaultValue="creditcard">
                  <Stack>
                    <Radio size="md" value="paypal" colorScheme="teal">
                      <Box display="inline">
                        <Image
                          src={paypal}
                          style={{ display: "inline" }}
                          mr={2}
                        />
                        PAYPAL
                      </Box>
                    </Radio>
                    <Radio
                      size="md"
                      value="creditcard"
                      colorScheme="teal"
                      defaultChecked
                    >
                      <Box display="inline">
                        <Image
                          src={creditcard}
                          style={{ display: "inline" }}
                          mr={2}
                        />
                        CREDIT CARD
                      </Box>
                    </Radio>
                  </Stack>
                </RadioGroup>
              </Box>
            </Box>
            <MenhirOrderSummary />
          </SimpleGrid>
        )}

        <Center mt={4}>
          <Link to="/checkoutship">
            <Button
              mt={10}
              mb={10}
              backgroundColor="lightgray"
              size="md"
              fontWeight="normal"
              mr={4}
            >
              INDIETRO
            </Button>
          </Link>
          <Link to="/ordercompleted">
            <Button
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade"}}
              size="md"
              fontWeight="normal"
            >
              CONFERMA
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
