import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  useLocation,
  useHistory,
} from "react-router-dom";
import {
  Box,
  Flex,
  Button,
  Divider,
  SimpleGrid,
  List,
  ListItem,
  ListIcon,
  Heading,
  Textarea,
  Text,
  Center,
  Image,
  FormControl,
  FormLabel,
  FormErrorMessage,
  FormHelperText,
  Input,
} from "@chakra-ui/react";
import MenhirWindow from "./Window";

const fakeAuth = {
  isAuthenticated: false,
  authenticate(cb) {
    this.isAuthenticated = true;
    setTimeout(cb, 100); // fake async
  },
  signout(cb) {
    this.isAuthenticated = false;
    setTimeout(cb, 100); // fake async
  },
};

export default function MenhirLogin() {
  const [redirectToReferrer, setRedirectToReferrer] = React.useState(false);
  const { state } = useLocation();

  const login = () =>
    fakeAuth.authenticate(() => {
      setRedirectToReferrer(true);
    });

  if (redirectToReferrer === true) {
    return <Redirect to={state?.from || "/"} />;
  }

  return (
    <Box flex="1" p={0}>
      <MenhirWindow showHomeBanner={false} />
      <Center>
        <Box width={{ base: "100%", md: "20%" }} p={{ base: 5, md: 0 }}>
          <Box>
            <Text as="h3" mt={{ base: 0, md: 10 }}>
              LOGIN
            </Text>
          </Box>
          <FormControl id="username">
            <FormLabel>User Name</FormLabel>
            <Input type="email" />
          </FormControl>
          <FormControl id="password" mt={4}>
            <FormLabel>Password</FormLabel>
            <Input type="password" />
          </FormControl>
          <Button
            onClick={login}
            bg="brand.100"
            color="#ffffff"
            _hover={{ bgColor: "brand.100shade" }}
            size="md"
            mt={4}
          >
            Login
          </Button>
        </Box>
      </Center>
    </Box>
  );
}

export function MenhirPrivateRoute({ children, ...rest }) {
  const [redirectToReferrer, setRedirectToReferrer] = React.useState(false);
  const { state } = useLocation();

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return fakeAuth.isAuthenticated === true ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
}
