import imgWelcome from "./home-landscape.png";
import imgWelcomePromo from "./home-landscape-promo.png";
import imgWelcomeSmall from "./home-landscape-small.png";
import imgWelcomeSmallTwo from "./home-landscape-small-2.png";
import { Box, HStack, Text } from "@chakra-ui/react";
import React, { useState, useEffect } from "react";
import { Link } from 'react-router-dom';
import Slider from "react-slick";
import "./slick.css";
import "./slick-theme.css";

const MenhirWindow = (props: any) => {
  const [categories, setCategories] = useState([]);

  const getCategories = () => {
    fetch("test-data-categories.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    })
      .then(function (response) {
        console.log(response);
        return response.json();
      })
      .then(function (myJson) {
        console.log(myJson);
        setCategories(myJson);
      });
  };

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };  

  useEffect(() => {
    getCategories();
  }, []);

  return (
    <Box>
      <Box
        bg="white"
        w="100%"
        borderColor="gray.200"
        display={{ base:'none', lg:'flex'}}
        flexDir={{ base: "column-reverse", md: "row" }}
        justifyContent="center"
        color="teal.600"
        borderBottom="1px"
        borderBottomColor="teal.600"
      >
        <HStack spacing="xs" bg="white" textAlign="center" mt="3" mb="4">
          {categories.map((item, index) => (
            <Box
              key={index}
              w="xs"
              h={5}
              pb={10}
              style={{ "textTransform": "uppercase", "cursor":"pointer" }}
              display="inline-block"
            >
              <Link key={index} to={{ pathname:"/browser", state:[{catId:`${item.code}`}, {catDesc:`${item.title}`}] }} >
                <Text _hover={{ fontWeight:'bold' }} as="h4" mr={6} fontSize="md">{item.title}</Text>
              </Link>
            </Box>
          ))}
        </HStack>
      </Box>
      { props.showHomeBanner && 
        <Slider {...settings}>
                <Box
        h="300px"
        bg="white"
        bgImage={{ base: imgWelcomeSmall, md: imgWelcomeSmall, lg: imgWelcome }}
        bgPosition="center"
        bgRepeat="no-repeat"
        bgSize="1800 360px"
      ></Box>
                <Box
        h="300px"
        bg="white"
        bgImage={{ base: imgWelcomeSmallTwo, md: imgWelcomeSmallTwo, lg: imgWelcomePromo }}
        bgPosition="center"
        bgRepeat="no-repeat"
        bgSize="1800 360px"
      ></Box>      
        </Slider>      
      }
    </Box>
  );
};

export default MenhirWindow;
